import React from "react";
import styled from "styled-components";

export default class ComponentToPrint extends React.Component {
  render() {
    const {
      Companies,
      Operations,
      Clients,
      discount,
      additional,
      type,
      total,
    } = this.props.note;

    return (
      <Wrapper>
        <Value>
          Operação: {type === "1" ? <Buy>Compra</Buy> : <Sell>Venda</Sell>}
        </Value>
        <Value>Empresa: {Companies.name}</Value>
        <ItemTitle>
          <Paragraph>Produto</Paragraph>
          <Paragraph>Quantidade</Paragraph>
          <Paragraph>Valor</Paragraph>
          <Paragraph>Total</Paragraph>
        </ItemTitle>
        {Operations.map((item) => (
          <Item key={item.id}>
            <Paragraph>{item.Products.name}</Paragraph>
            <Paragraph>{item.amount} kg</Paragraph>
            <Paragraph>R$ {item.value}/kg</Paragraph>
            <Paragraph>
              R$ {Number(item.amount * item.value).toFixed(2)}
            </Paragraph>
          </Item>
        ))}
        <Value>Desconto: R$ {discount}</Value>
        <Value>Adicional: R$ {additional}</Value>
        <Value>Cliente: {Clients.name}</Value>
        <Value>Total: R$ {total}</Value>
      </Wrapper>
    );
  }
}

const Wrapper = styled.div`
  margin: 30px auto;
  padding: 30px;
  width: 100%;
  height: 100%;
`;

const Item = styled.div`
  width: 100%;
  height: 30px;
  display: flex;
  justify-content: space-around;
  aling-items: center;
  border: 1px solid gray;
  margin: 5px 0;
`;

const ItemTitle = styled(Item)`
  border: 0;
`;

const Value = styled.p`
  text-align: center;
`;

const Paragraph = styled.p`
  text-align: center;
  width: 25%;
  background: gray;
  margin: auto;
`;

const Buy = styled.span`
  color: red;
`;

const Sell = styled.span`
  color: green;
`;
